import React from "react";
import styled from "styled-components";
import Fade from "react-reveal/Fade";

function AboutMe() {
  return (
    <SectionTwo>
      <Fade left>
        <Subtitle>
          <h1 id="two">Обо мне</h1>
        </Subtitle>
      </Fade>
      <AboutMeSect>
        <Fade left>
          <ImageInfo src="/images/3otv.gif" />
        </Fade>
        <AboutInfo>
          <Fade duration={1000} top>
            <AboutTitle>
              Разработчик пользовательских интерфейсов, с опыт работы более 1.5 лет.
              Соблюдаю принципы написания кода и профессионально выполняю задачи.
            </AboutTitle>
          </Fade>
          <Fade top>
            <a href="https://disk.yandex.ru/i/bB-9uTCl1FmkHg">
              <DownloadCv>скачать резюме</DownloadCv>
            </a>
          </Fade>
        </AboutInfo>
      </AboutMeSect>
    </SectionTwo>
  );
}

export default AboutMe;

const AboutMeSect = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  @media (max-width: 768px) {
    flex-direction: column;
  }
`;

const SectionTwo = styled.div`
  width: 100vw;
`;

const AboutInfo = styled.div`
  letter-spacing: 1.2px;
  font-size: 18px;
  text-align: start;
  display: flex;
  flex-direction: column;
  justify-content: center;
  @media (max-width: 768px) {
    align-items: center;
    text-align: center;
  }
`;

const ImageInfo = styled.img`
  width: 400px;
  border-radius: 60px;
  margin-right: 20px;
  @media (max-width: 768px) {
    width: 200px;
    margin: 15px auto;
  }
`;

const DownloadCv = styled.button`
  font-size: 15px;
  background: #55198b;
  color: #fff;
  width: 150px;
  height: 56px;
  cursor: pointer;
  margin-top: 18px;
  padding: 15px;
  border: none;
  border-radius: 5px;
  transition: background .3s ease-in-out 0s;
  &:hover {
    transition: all .3s ease-in 0s;
    background: #f5f11c;
    color: #000;
  }
  a {
    text-decoration: none;
    color: #fff;
    &:hover {
      color: #000;
    }
  }
`;

const AboutTitle = styled.p`
  font-size: 18px;
  padding: 0 20px;
  line-height: 1.5;
`;

const Subtitle = styled.div``;
