import React from "react";
import styled from "styled-components";
import GitHubIcon from "@material-ui/icons/GitHub";
import LinkedInIcon from "@material-ui/icons/LinkedIn";
import InstagramIcon from "@material-ui/icons/Instagram";
import TelegramIcon from "@material-ui/icons/Telegram";
import Fade from "react-reveal/Fade";
import { ThemeSwitcher } from "../ThemeSwitcher";

function Footer() {
  const themesColor = React.useContext(ThemeSwitcher);

  return (
    <Fade left>
      <Container>
        <LeftContent>
          <h1>
            Контакты <img src="images/smartphone.png" alt="phone" />
          </h1>
          <h3>
           Есть предложения? Напишите мне на почту или в социальных сетях 
          </h3>
          <p>
            Email:<a href="mailto:rinni499@gmail.com">rinni499@gmail.com</a>{" "}
          </p>
          <Links>
            <a href="https://github.com/r33n99">
              <GitHubIcon />
            </a>
            <a href="https://www.linkedin.com/in/rinat-razhapov-8998a0209/">
              <LinkedInIcon />
            </a>
            <a href="https://www.instagram.com/r33n99/">
              <InstagramIcon />
            </a>
            <a href="https://t.me/r33n99">
              <TelegramIcon />
            </a>
          </Links>
          
        </LeftContent>
        
      </Container>
    </Fade>
  );
}

export default Footer;

const Container = styled.div`
  background: ${(themesColor) => themesColor.theme.back};
  margin: 0 auto;
  font-size: 20px;
  display: flex;
  justify-content: space-between;
  @media (max-width: 768px) {
    font-size: 12px;
    margin-top: 25px;
  }
`;

const LeftContent = styled.div`
  color: ${(themesColor) => themesColor.theme.title};
  padding: 15px;
  letter-spacing: 2px;
  line-height: 2;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  a {
    color: ${(themesColor) => themesColor.theme.title};
    text-decoration: none;
    &:hover {
      color: ${(themesColors) => themesColors.theme.url};
    }
  }
  img {
    width: 50px;
  }
`;
const Links = styled.div`
  display: flex;
  margin: 0 auto;
  a {
    margin: 10px 10px;
  }
`;
